.dashboard {
  display: flex;
  gap: 20px;
  padding: 20px;
  flex-wrap: wrap;
  justify-content: center;
}
.panel {
  background: #1f1f1f;
  padding: 20px;
  border-radius: 12px;
  width: 300px;
  cursor: pointer;
  transition: 0.3s ease;
}
.panel:hover {
  background: #2c2c2c;
}
.modal {
  position: fixed;
  top: 0; left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 10;
}
.modal-content {
  background: #222;
  padding: 30px;
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
}
.modal-content h2 {
  margin-top: 0;
}
.task-list {
  list-style: none;
  padding: 0;
}
.task-list li {
  padding: 8px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #444;
}
input, button {
  padding: 10px;
  border: none;
  margin-top: 10px;
  width: 100%;
  border-radius: 6px;
}
button {
  background: #444;
  color: white;
  cursor: pointer;
  margin-top: 5px;
}
button:hover {
  background: #666;
}
.close-btn {
  position: absolute;
  top: 20px;
  right: 30px;
  background: transparent;
  color: white;
  font-size: 28px;
  border: none;
  cursor: pointer;
}